.timer-container{
    position: relative;
    text-align: center;
    user-select: none;
}

.timer{
    position: absolute;
    top: 60%;
    left: 50%;
    color: white;
    font-size: 75px;
    transform: translate(-50%, -50%);
}

#count{
    position: absolute;
    top: 85%;
    left: 50%;
    color: white;
    font-size: 25px;
    transform: translate(-50%, -50%);
}

.image{
    width: 350px;
}